body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  font-family: "Verdana", cursive, sans-serif;
  color: #ffffff;
}
#root {
  display: flex;
  justify-content: center;
  align-items: center;
}
body {
  background-color: #e9e9f3;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.container {
  width: 100%;
  min-height: 100vh;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.result-section {
  display: flex;
  flex-direction: column;
  font-size: 24px;
  align-items: center;
}

.question-section {
  width: 100%;
  position: relative;
  justify-items: center;
  display: grid;
}

.question-count {
  margin-bottom: 30px;
  color: #18191a;
}

.question-count span {
  font-size: 28px;
  color: #18191a;
}

.question-text {
  margin-bottom: -15px;
  font-size: 20px;
  color: #18191a;
}

.answer-section {
  width: 550px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

button {
  width: 100%;
  margin-top: 10px;
  font-size: 16px;
  color: #ffffff;
  background-color: #4b65c2;
  border-radius: 5px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 2px solid #0a2a49;
  cursor: pointer;
  transition: transform 0.3s;
}

button:hover {
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.31);
  background-color: #0d2886;
  transform: scale(1.03);
}

#handlePlayAgainButtonClick {
  align-items: center;
  display: flex;
  justify-content: center;
  justify-items: center;
}

table {
  width: 100%;
}
table,
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
}
th,
td {
  padding: 15px;
  text-align: left;
  color: #18191a;
  font-size: medium;
  font-weight: 700;
}
#t01 tr {
  background-color: #fff;
}
#t01 th {
  background-color: black;
  color: white;
}

@media screen and (max-width: 450px) {
  .container {
    width: 300px;
  }
  button {
    margin-top: 20px;
    padding: 12px;
  }
  .answer-section {
    width: 100%;
  }
}
@media screen and (max-width: 330px) {
  .container {
    max-width: 250px;
  }
}

@media screen and (min-width: 400px) {
  .container {
    min-width: 350px;
  }
  button {
    margin-top: 25px;
    padding: 12px;
  }
}

@media screen and (min-width: 700px) {
  .container {
    min-width: 550px;
  }
  button {
    margin-top: 20px;
    padding: 15px;
  }
}
